'use client'

import { useState } from 'react'
import { Button } from "@/components/ui/button"
import Link from "next/link"

const exampleData = [
  {
    id: 1,
    name: 'Hytte 15A - Høgeset',
    address: 'Høgesetvegen 15A, 3540 Nesbyen',
    status: 'Planlagt'
  },
  {
    id: 2,
    name: 'Hytte 23B - Høgeset',
    address: 'Høgesetvegen 23B, 3540 Nesbyen',
    status: 'Kritisk'
  }
]

export default function InspeksjonerDashboard() {
  const [inspections] = useState(exampleData)

  return (
    <div className="max-w-7xl mx-auto p-4">
      <div className="flex justify-between items-center mb-6">
        <h1 className="text-2xl font-bold">Inspeksjoner</h1>
        <Link href="/inspeksjoner/ny">
          <Button className="bg-green-600 hover:bg-green-700">
            + Ny inspeksjon
          </Button>
        </Link>
      </div>

      <div className="grid grid-cols-4 gap-4 mb-8">
        <div className="bg-blue-50 p-4 rounded-lg">
          <h3 className="text-lg font-semibold text-blue-800">Planlagte</h3>
          <p className="text-3xl font-bold text-blue-600">1</p>
        </div>
        <div className="bg-green-50 p-4 rounded-lg">
          <h3 className="text-lg font-semibold text-green-800">Utført</h3>
          <p className="text-3xl font-bold text-green-600">0</p>
        </div>
        <div className="bg-orange-50 p-4 rounded-lg">
          <h3 className="text-lg font-semibold text-orange-800">Haster</h3>
          <p className="text-3xl font-bold text-orange-600">0</p>
        </div>
        <div className="bg-red-50 p-4 rounded-lg">
          <h3 className="text-lg font-semibold text-red-800">Kritiske</h3>
          <p className="text-3xl font-bold text-red-600">1</p>
        </div>
      </div>

      <div className="space-y-4">
        {inspections.map((inspection) => (
          <div 
            key={inspection.id}
            className="bg-white p-4 rounded-lg shadow"
          >
            <h2 className="text-lg font-semibold">{inspection.name}</h2>
            <p className="text-gray-600">{inspection.address}</p>
            <p className={`mt-2 inline-block px-2 py-1 rounded text-sm ${
              inspection.status === 'Kritisk' 
                ? 'bg-red-100 text-red-800' 
                : 'bg-blue-100 text-blue-800'
            }`}>
              {inspection.status}
            </p>
          </div>
        ))}
      </div>
    </div>
  )
} 